@import "../../globalSASS/global.scss";
.count {
    width: 660px;
    height: 80px;

    font-family: 'Roboto', sans-serif;
    font-size: 1.3rem;

    background: rgb(243, 194, 104);

    transition: ease 0.1s;

    cursor: pointer;

    &:hover {
        background-color: #fff;
    }

    &:active {
        transform: scale(0.98);
    }
}