@import "../../globalSASS/global.scss";
.container {
	position: relative;
	margin-left: auto;
	margin-right: auto;

	width: 720px;
	min-height: 100vh;

	border-right: 1px solid #000;
	border-left: 1px solid #000;

	&__title {
		margin: 0;
		margin-left: 15px;
		padding-top: 20px;
    }
    
    &__questions {
        margin: 30px;
	}
	
	&__count {
		margin-left: 30px;
	}

	&__result {
		margin-left: 30px;
		margin-top: 60px;
	}

	&__reset { 
		position: absolute;
		left: 200px;
		top: 35px;
	}
}
