@import "../../globalSASS/global.scss";
.result {

    position: relative;

    &__block {
        margin-left: 15px;
    }

    &__scale {
        margin-top: 30px;
        margin-left: 15px;

        font-style: italic;
        font-weight: bold;
        
    }

    &__criteria {
        margin-left: 30px;
        margin-bottom: 10px;

        display: flex;
        flex-direction: column;

        
    }


    &__point {
        margin-top: 30px;
        margin-bottom: 5px;

        font-style: italic;

        &:first-child {
            margin-top: 5px;
        }
    }

    &__criterion {
        margin-left: 15px;

        display: flex;
        width: 300px;

        justify-content: space-between;
        align-items: center;
    }

    &__positive {
        color: #267217;
        font-weight: bold;
        text-decoration: underline;
    }

    &__copy {
        position: absolute;
        left: 310px;
        top: 10px;
    }
}